.page-footer {
  position: relative;

  padding-bottom: 0;

  border-top: 3px solid $orange;
}

.page-footer__container {
  display: flex;

  flex-wrap: wrap;
}

.page-footer__info {
  margin: 0;
  padding: 0;

  list-style: none;

  text-align: center;
}

.page-footer__img {
  width: 111px;
  height: 30px;
}

.page-footer__column {
  width: 100%;
  margin-right: 0;

  color: $gray;

  a {
    display: block;

    transition: all 0.3s ease-out;

    &:hover {
      color: $orange;
    }
  }
}

.page-footer__title {
  margin: 0;
  margin-bottom: 15px;

  font-size: 18px;
  font-weight: 700;

  color: #ffffff;
}

.page-footer__logo {
  display: flex;

  width: 100%;
  padding: 30px;

  flex-wrap: wrap;
  justify-content: center;
}

.page-footer__info {
  width: 100%;
}

.page-footer__services {
  margin: 0;
  padding: 0;

  list-style: none;
}

.page-footer__link {
  margin-bottom: 10px;

  text-decoration: none;

  color: $gray;

  &--big {
    margin-bottom: 15px;

    font-size: 20px;
  }
}

.page-footer__social {
  display: flex;

  margin: 0;
  padding: 0;

  list-style: none;

  justify-content: center;

  li {
    margin-right: 30px;

    &:last-of-type {
      margin-right: 0;
    }
  }

  a {
    color: $gray;
  }
}

@media (min-width: $tablet-width) {
  .page-footer {
    padding: 3rem 0;
  }

  .page-footer__logo {
    padding-left: 0;
  }

  .page-footer__img {
    width: 182px;
    height: 49px;
  }

  .page-footer__container {
    box-sizing: border-box;
    max-width: 1160px;
    padding: 0 70px;
  }

  .page-footer__logo {
    margin-bottom: 20px;
  }

  .page-footer__info {
    display: flex;

    text-align: left;

    flex-wrap: wrap;
  }

  .page-footer__column {
    width: 48%;
    margin-bottom: 3rem;

    font-size: 16px;
  }

  .page-footer__title {
    margin-bottom: 30px;
  }

  .page-footer__address {
    margin-bottom: 0;
  }

  .page-footer__social {
    justify-content: flex-start;
  }
}

@media (min-width: $desktop-width) {
  .page-footer__logo {
    justify-content: flex-start;
  }

  .page-footer__info {
    justify-content: space-between;
  }

  .page-footer__column {
    width: auto;
  }

  .page-footer__link--big {
    font-size: initial;
  }
}
