.help {
  width: 100%;
  margin-bottom: 146px;
}

.help__list {
  margin: 0;
  padding: 0;

  list-style: none;
}

.help__question {
  position: relative;

  margin-bottom: 20px;
  padding: 10px;
  padding-right: 10%;

  cursor: pointer;
  transition: font-size 1s ease-out, line-height 1s ease-out;

  font-size: 20px;
  font-weight: 700;
  line-height: 24px;

  border: 1px solid $orange;

  &:hover,
  &:focus {
    color: $orange;
    border-color: $white;
  }

  &:last-of-type {
    margin-bottom: 0;
  }

  &::before {
    position: absolute;
    right: 0;

    content: '+';
    transform: translate(-50%, 10%);

    font-size: 50px;
    font-weight: 500;

    color: $orange;
  }

  &.accordeon__button--opened {
    margin-bottom: 0;

    font-size: 28px;
    line-height: 34px;

    background-color: $orange;

    &::before {
      content: '-';
      transform: translate(-70%, 10%);

      color: $white;
    }
  }
}

.help__answer {
  line-height: 21px;
}

.help__answer.accordeon__content {
  padding: 0 20px;
}

.help__answer.accordeon__content--opened {
  padding: 20px;
}

@media(min-width: $tablet-width) {
  .help__question {
    padding: 17px;

    font-size: 22px;
    line-height: 26px;

    &.accordeon__button--opened {
      font-size: 22px;
      line-height: 26px;
    }
  }
}

@media(min-width: $desktop-width) {
  .help {
    margin-bottom: 290px;
  }

  .help__question {
    padding: 27px 47px;

    font-size: 29px;
    line-height: 35px;

    border: 2px solid $orange;

    &.accordeon__button--opened {
      font-size: 29px;
      line-height: 35px;
    }
  }

  .help__answer.accordeon__content--opened {
    font-size: 18px;
    line-height: 27px;
  }
}
