
.intro {
  padding-top: 64px;
  padding-bottom: 100px;
}

.intro__button {
  width: 247px;
  margin-bottom: 30px;

  &:last-of-type {
    display: flex;

    margin: 0;

    flex-wrap: wrap;
    justify-content: space-between;
  }
}

.intro__description {
  margin-bottom: 25px;
}

@media(min-width: $tablet-width) {
  .intro {
    padding: 122px 0 128px 0;
  }

  .intro__title {
    max-width: 721px;
  }

  .intro__buttons {
    display: flex;

    min-width: 577px;
  }

  .intro__button {
    min-width: auto;
    margin-right: 15px;
    margin-bottom: 0;
    padding: 20px 15px;

    flex-grow: 1;

    &:last-of-type {
      flex-grow: 2;
    }
  }

  .intro__description {
    max-width: 454px;

    letter-spacing: 0.01em;

    line-height: 27px;
  }
}

@media(min-width: $desktop-width) {
  .intro {
    padding-left: 0;
  }

  .intro__description {
    max-width: 610px;
    margin-bottom: 55px;

    line-height: 33px;
  }

  .intro__button {
    margin-right: 50px;
    padding: 20px 30px;

    flex-grow: 2;

    &:last-of-type {
      flex-grow: 3;
    }
  }
}
