.do {
  position: relative;

  width: 100%;
  margin-bottom: 147px;

  .swiper-button-next {
    top: 58%;
    right: -20px;

    transition: all 0.5s ease-out;

    color: $orange;
    outline: none;

    &:hover,
    &:focus {
      color: $white;
    }
  }

  .swiper-button-prev {
    top: 58%;
    left: -20px;

    transition: all 0.5s ease-out;

    color: $orange;
    outline: none;

    &:hover,
    &:focus {
      color: $white;
    }
  }

  .swiper-pagination {
    display: none;
  }
}

.do__list {
  margin: 0;
  padding: 0;

  list-style: none;
}

.do__item {
  min-height: 287px;
}

.do .swiper-container {
  position: initial;

  width: 90%;
}

@media(min-width: 1045px) {
  .do {
    .swiper-pagination {
      display: block;

      height: 1px;

      border-radius: 10px;
      background-color: rgb(254,176,23);
    }

    .swiper-container-horizontal > .swiper-pagination-bullets {
      bottom: -45px;
    }

    .swiper-pagination-bullet {
      position: relative;
      bottom: 12px;

      width: 120px;
      height: 4px;

      transition: all 0.3s ease-in;

      opacity: 0;
      border-radius: 10px;
      background-color: rgb(254,176,23);
    }

    span.swiper-pagination-bullet.swiper-pagination-bullet-active {
      opacity: 1;
    }
  }
}

@media(min-width: 1200px) {
  .do {
    .swiper-pagination-bullet {
      bottom: 17px;
    }
  }
}
