html {
  box-sizing: border-box;
  min-height: 100vh;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  position: relative;

  display: flex;
  flex-direction: column;

  min-height: 100vh;
  margin: 0;
  padding: 0;

  hyphens: auto;

  font-family: 'Montserrat', 'Arial',sans-serif;
  font-size: 14px;
  font-weight: 400;
  font-style: normal;

  color: $white;
}

main {
  flex-grow: 1;

  &.container {
    padding-top: 100px;
  }
}

address {
  font-style: normal;
}

.container {
  display: flex;

  max-width: 1500px;
  margin: 0 auto;
  padding: 0 25px;

  flex-wrap: wrap;

  &--case {
    .project__title {
      display: none;

      &--similar {
        display: block;
      }
    }

    .project__item:nth-of-type(n+3) {
      display: none;
    }
  }
}

.background {
  background-color: $black;
  background-image: url('../img/bg-mobile.png');
  background-repeat: no-repeat;
  background-position: 0 0;
  background-size: cover;

  flex-grow: 1;
}

.about-feedback {
  margin-bottom: 80px;
}

@media(min-width:$tablet-width) {
  .about-feedback {
    margin-bottom: 190px;
  }

  .container {
    padding: 0 128px;
  }

  body {
    font-size: 16px;
  }

  .background {
    background-image: url('../img/bg-tablet.png');
  }

  .container {
    &--case {
      .project {
        margin-bottom: 100px;
      }

      .project__item:nth-of-type(n+3) {
        display: block;
      }
    }
  }
}

@media(min-width:$desktop-width) {
  body {
    font-size: 21px;
    line-height: 26px;
  }

  .container {
    width: 100%;
    padding: 0 80px;

    &--case {
      .project {
        margin-bottom: 200px;
      }
    }
  }

  .about-feedback {
    display: flex;

    width: 100%;
    margin-bottom: 295px;

    justify-content: space-between;
  }

  .about-feedback__item {
    width: 45%;
  }

  .background {
    background-image: url('../img/bg-desktop.png');
  }
}

.flip-container {
  perspective: 1000;
}

.flip-container:hover .flipper,
.flip-container.hover .flipper {
  transform: rotateY(180deg);
}

.flip-container,
.front,
.back {
  width: 320px;
  height: 480px;
}

.flipper {
  position: relative;

  width: 100%;
  height: 100%;

  transition: 1s;

  transform-style: preserve-3d;
}

.front,
.back {
  position: absolute;
  top: 0;
  left: 0;

  backface-visibility: hidden;
}

.front {
  z-index: 2;
}

.back {
  transform: rotateY(180deg);
}

.flash {
  position: absolute;
  top: 0;
  left: -4.5em;

  display: block;

  width: 3em;
  height: 100%;

  content: '';
  transition: none;
  transform: skewX(-45deg) translateX(0);

  background-color: rgba(255,255,255,0.5);;
}

@keyframes moving {
  30% {
    transform: skewX(-45deg) translateX(50em);
  }

  100% {
    transform: skewX(-45deg) translateX(50em);
  }
}

.lighting {
  -moz-animation: moving 3.5s ease-in-out infinite;
  -ms-animation: moving 3.5s ease-in-out infinite;
  -o-animation: moving 3.5s ease-in-out infinite;
  animation: moving 3.5s ease-in-out infinite;
}

