.header {
  position: fixed;
  z-index: 13;

  width: 100%;

  &.background .header__container {
    padding-top: 0;
    padding-bottom: 0;
  }
}

.header__container {
  position: relative;

  display: flex;

  width: 100%;
  padding-top: 25px;
  padding-bottom: 25px;

  transition: padding 0.5s ease-out;

  flex-wrap: wrap;
  justify-content: space-between;
}

.header__logo {
  z-index: 2;
}

.header__logo:hover,
.header__logo:focus {
  opacity: 0.7;
}

.header__burger {
  z-index: 1;

  @include button;
}

.header__burger--closed {
  display: none;
}

.header__close {
  z-index: 2;

  @include button;
}

.header__close--closed {
  display: none;
}

.header__burger-img {
  fill: $white;
}

.header__img {
  width: 111px;
  height: 30px;
}

@media(min-width:$tablet-width) {
  .header {
    display: flex;

    align-items: center;
    justify-content: space-between;
  }

  .header__burger {
    display: none;
  }

  .header__container {
    margin: 0 auto;
    padding-top: 19px;
  }

  .header__logo {
    flex-grow: 1;
  }

  .header__img {
    width: 182px;
    height: 49px;
  }
}
