.service {
  padding-top: 64px;
}

.service__list {
  margin: 0;
  padding: 0;

  list-style: none;
}

.service__item {
  box-sizing: border-box;
  margin-bottom: 54px;

  &:last-of-type {
    margin-bottom: 54px;
  }
}

.service__subtitle {
  margin: 0;
  margin-bottom: 20px;

  font-size: 24px;
  font-weight: 600;
  line-height: 39px;
}

.service__description {
  margin: 0;
  margin-bottom: 25px;

  font-size: 14px;
  line-height: 22px;
}

.service__link {
  display: block;

  width: 220px;

  text-decoration: none;
  text-transform: lowercase;

  font-weight: 600;
}

@media(min-width: $tablet-width) {
  .service {
    margin-bottom: 200px;
    padding-top: 122px;
  }

  .service__list {
    display: flex;

    flex-wrap: wrap;
    justify-content: space-between;
  }

  .service__item {
    display: flex;
    flex-direction: column;

    width: 48%;

    justify-content: space-between;
  }

  .service__subtitle {
    font-weight: 700;
    line-height: 29px;
  }
}

@media(min-width: $desktop-width) {
  .service {
    padding-top: 169px;
  }

  .service__item {
    width: 33.33%;
    padding: 34px 36px;

    &:hover,
    &:focus {
      background-color: $orange;

      .service__link {
        color: $black;
        background-color: $white;
      }
    }
  }

  .service__subtitle {
    line-height: 30px;
    line-height: 36px;
  }

  .service__description {
    font-size: 21px;
    line-height: 26px;
  }
}
