.feedback-only {
  margin-bottom: 78px;
  padding-top: 30px;

  border-top: 3px solid #ff7e07;
}

.feedback__input {
  margin-bottom: 20px;
}

.feedback__label:last-of-type .feedback__input {
  margin-bottom: 40px;
}

.feedback__input {
  width: 100%;
  margin-bottom: 20px;
  padding: 15px 30px;

  transition: all 0.5s ease-out;

  font-family: 'Montserrat', 'Arial',sans-serif;
  font-size: 17px;

  color: $white;
  border: solid 2px $orange;
  outline: none;
  background: transparent;

  &::placeholder {
    color: $white;
  }

  &:focus,
  &:hover {
    border-color: $white;
    border-radius: 10px;
  }
}

.feedback__button {
  position: relative;

  overflow: hidden;

  width: 100%;
  padding: 15px;

  cursor: pointer;
  transition: all 0.5s ease-out;
  letter-spacing: 0.05em;
  text-transform: uppercase;

  font-family: 'Montserrat', 'Arial',sans-serif;
  font-weight: 700;

  color: $white;
  border: none;
  background-image: linear-gradient(45deg,#fd6c03,#feb017);

  &:focus,
  &:hover {
    color: $black;
    border-radius: 10px;
    background-image: linear-gradient(45deg,#feb017,#fd6c03);
  }
}

@media(min-width: $tablet-width) {
  .feedback-only {
    margin-bottom: 190px;

    border-top: 0;

    .feedback {
      font-size: 46px;
      line-height: 61px;
    }

    .feedback__input {
      padding: 18px 30px;
    }
  }

  .feedback__button {
    max-width: 274px;
  }
}

@media(min-width: $desktop-width) {
  .feedback-only {
    margin-bottom: 180px;

    .container {
      display: flex;

      justify-content: flex-end;
    }
  }

  .feedback {
    width: 100%;
    max-width: 580px;
    margin-bottom: 0;
  }

  .feedback__title {
    margin-bottom: 50px;
  }
}
