.do-card {
  display: flex;
  flex-direction: column;

  box-sizing: border-box;
  padding: 27px 30px;

  border: solid 3px $orange;

  flex-wrap: wrap;
  justify-content: space-between;
}

.do-card__description {
  margin: 0;
}

.do-card__title {
  margin: 0;

  word-break: break-word;

  font-size: 22px;
  line-height: 26px;
}

.do-card__link {
  position: relative;

  width: fit-content;

  transition: all 0.2s ease-out;
  text-decoration: none;
  text-transform: lowercase;

  font-size: 12px;
  line-height: 15px;

  color: $white;

  &:hover,
  &:focus {
    font-weight: 600;

    color: $orange;

    svg {
      fill: $white;
    }
  }

  svg {
    position: absolute;
    top: -7px;
    right: -35px;

    transform: rotate(90deg);

    fill: $orange;
  }
}

@media(min-width: $tablet-width) {
  .do-card {
    min-height: 318px;
  }

  .do-card__title {
    font-size: 25px;
    line-height: 30px;
  }

  .do-card__description {
  }
}

@media(min-width: $desktop-width) {
  .do-card {
  }

  .do-card__title {
  }

  .do-card__description {
    font-size: 16px;
    line-height: 20px;
  }
}
