.case {
  width: 100%;
  margin-bottom: 80px;
}

.case__type {
  width: fit-content;
  margin-bottom: 27px;
  padding: 5px 20px;
}

.case__container {
  display: flex;

  flex-wrap: wrap;
  justify-content: center;
}

.case__title {
  margin-bottom: 0;
}

.case__task {
  margin-bottom: 30px;
}

.case__subtitle {
  margin: 0;
  margin-bottom: 15px;
}

.case__description {
  margin: 0;

  line-height: 21px;
}

.case__img {
  width: 100%;
  height: auto;
  padding-top: 50px;
}

@media(min-width: $tablet-width) {
  .case__container {
    max-width: 670px;
    margin: 0 auto;

    justify-content: flex-start;
  }

  .case__type {
    margin-top: 48px;
    margin-bottom: 40px;
  }

  .case__task {
    margin-bottom: 50px;
  }

  .case__title {
    width: 400px;
  }

  .case__img {
    width: 673px;
    height: 534px;
  }
}

@media(min-width: $desktop-width) {
  .case {
    position: relative;

    margin-bottom: 120px;
  }

  .case__container {
    max-width: 100%;

    justify-content: flex-end;
  }

  .case__tasks {
    display: flex;

    justify-content: space-between;

    .case__task {
      width: 47%;
    }

    .case__description {
      width: 100%;
    }
  }

  .case__task {
    display: flex;

    justify-content: center;
    flex-wrap: wrap;
  }

  .case__description {
    width: 65%;
    margin-right: auto;
  }

  .case__title {
    position: absolute;
    top: 200px;
    left: 0;

    width: 620px;
  }

  .case__subtitle {
    width: 100%;
  }

  .case__type {
    position: absolute;
  }

  .case__description {
    font-size: 24px;
    line-height: 33px;
  }

  .case__img {
    width: 787px;
    height: 606px;
  }
}
