@mixin fontface($family, $localname, $filename, $weight, $style) {
  @font-face {
    font-family: $family;
    font-weight: $weight;
    font-style: $style;

    font-display: swap;
    src: local('#{$localname}'),
    url('../fonts/#{$filename}.woff') format('woff');;
  }
}
@include fontface('PT Sans', 'PtSans', PTSansRegular, 400, normal);
@include fontface('PT Sans', 'PtSans', PTSansBold, 600, normal);
@include fontface('PT Sans', 'PtSans', PTSansItalic, 400, italic);

@mixin visually-hidden {
  position: absolute;

  overflow: hidden;
  clip: rect(0 0 0 0);

  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;

  white-space: nowrap;

  border: 0;

  clip-path: inset(100%);
}

@mixin container {
  margin: 0 auto;
  padding-right: 20px;
  padding-left: 20px;

  @media (min-width: $tablet) {
  }

  @media (min-width: $small-screen) {
  }

  @media (min-width: $large-screen) {
    max-width: $large-screen;
  }
}

@mixin button {
  border: none;
  background: transparent;
}
