.project {
  display: flex;

  width: 100%;
  margin-top: 30px;
  margin-bottom: 88px;

  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-start;
}

.project__types {
  display: none;

  margin: 0;
  padding: 0;

  list-style: none;

  flex-wrap: wrap;
}

.project__title--similar {
  display: none;
}

@media(max-width:$tablet-width) {
  .projects {
    .project__types {
      display: block;

      width: 100%;
      margin-bottom: 64px;
    }

    .project__type {
      width: 220px;
      margin: 0 auto;
      margin-bottom: 22px;
      padding: 5px 20px;

      &:last-of-type {
        margin: 0 auto;
        margin-bottom: 0;
      }
    }
  }
}

.project__type {
  padding: 0 25px;

  cursor: pointer;
  transition: all 0.5s ease-out;
  text-align: center;
  text-transform: lowercase;

  font-size: 14px;

  border: solid 1px $orange;

  &.active {
    background-color: $orange;
  }

  &:last-of-type {
    margin-right: 0;
  }

  &:hover,
  &:focus {
    font-weight: 600;

    border-color: $white;
  }
}

.project__list {
  display: none;

  width: 0;
  max-width: 615px;
  height: 0;
  margin: 0;
  padding: 0;

  list-style: none;

  transition: all 1s ease-out;

  flex-wrap: wrap;
  justify-content: center;

  &.active {
    display: flex;

    width: auto;
    height: auto;
    margin-bottom: 20px;
  }
}

.project__item {
  position: relative;

  width: 308px;
  height: 219px;
  margin-bottom: 14px;

  @media(max-width:$tablet-width) {
    &:nth-of-type(n+5) {
      display: none;
    }
  }
}

.project__link {
  position: absolute;

  display: block;
  display: flex;
  flex-direction: column;

  width: 100%;
  height: 100%;

  transition: all 1s ease-out;
  text-decoration: none;

  color: $white;
  background-color: transparent;
  background-color: rgba(253, 108, 3,0.9);

  flex-wrap: wrap;
  justify-content: center;
}

.project__img {
  position: absolute;
  z-index: 0;
  top: 0;
  right: 0;

  width: 100%;
  height: 100%;
}

.project__name {
  width: 100%;
  margin: 0;
  margin-bottom: 20px;

  text-align: center;

  font-size: 30px;
  line-height: 15px;

  // opacity: 0;
}

.project__description {
  width: 100%;
  margin: 0;

  text-align: center;

  font-size: 14px;
  line-height: 17px;

  // opacity: 0;
}

.project__more-link {
  display: block;

  width: fit-content;
  margin: 0 auto;
  padding: 5px 25px;

  cursor: pointer;
  transition: all 0.5s ease-out;
  text-align: center;
  text-decoration: none;
  text-transform: lowercase;

  font-size: 14px;

  color: $white;
  border: solid 1px $orange;

  &:hover,
  &:focus {
    background-color: $orange;
  }
}

@media(min-width: $tablet-width) {
  .project {
    display: block;

    margin-top: 70px;
  }

  .project__title {
    justify-self: flex-start;
  }

  .project__types {
    display: flex;

    margin-bottom: 30px;

    justify-content: center;
  }

  .project__type {
    min-width: 130px;
    margin-right: 15px;
    padding: 5px 20px;

    font-size: 10px;
  }

  .project__list {
    max-width: 600px;
    margin: 0 auto;

    &.active {
      margin-bottom: 30px;
    }
  }

  .project__item {
    width: 279px;
    height: 198px;
    margin-right: 20px;
    margin-bottom: 16px;

    &:nth-of-type(2n) {
      margin-right: 0;
    }
  }
}

@media(min-width: 1360px) {
  .project__list {
    max-width: 100%;
  }

  .project__item {
    width: 380px;
    height: 270px;
    margin-right: 29px;
    margin-bottom: 25px;

    &:nth-of-type(2n) {
      margin-right: 29px;
    }

    &:nth-of-type(3n) {
      margin-right: 0;
    }
  }

  .project__type {
    min-width: 220px;
    margin-right: 29px;

    font-size: 14px;
  }

  .projects {
    .project__list {
      display: none;

      justify-content: space-between;

      &.active {
        display: flex;

        max-width: 1215px;
      }
    }

    .project__item {
      width: 585px;
      height: 414px;
      margin-right: 0;
      margin-bottom: 25px;
    }
  }
}
