.address {
  width: 100%;
  margin-top: 60px;
  margin-bottom: 86px;
}

.address__info {
  margin-bottom: 20px;

  font-size: 18px;
  line-height: 32px;

  color: $gray;

  &:last-of-type {
    margin-bottom: 40px;
  }
}

.address__time {
  margin: 0;
}

.address__map {
  width: 100%;
  height: 253px;
  margin-bottom: 52px;
}

.address__subtitle {
  margin: 0;
  margin-bottom: 15px;

  color: $white;
}

.address__link {
  text-decoration: none;

  font-size: 20px;
  line-height: 30px;

  color: $gray;
}

.address__tel {
  margin-right: 15px;

  font-size: 28px;
  line-height: 42px;

  color: $orange;
}

.address__socials .page-footer__social {
  justify-content: flex-start;
}

@media(max-width:540px) {
  .address__link {
    display: block;
  }
}

@media(min-width: $tablet-width) {
  .address__address {
    margin-bottom: 20px;
  }

  .address__map {
    height: 330px;
  }

  .address__info {
    display: flex;

    justify-content: space-between;
  }

  .address__subtitle {
    width: 48%;
  }

  .address__container {
    width: 48%;
  }

  .address__socials {
    display: none;
  }
}

@media(min-width: $desktop-width) {
  .address {
    margin-top: 100px;
  }

  .address__map {
    height: 686px;

    order: 1;
  }

  .address__wrapper {
    display: flex;

    flex-wrap: wrap;
  }

  .address__container {
    width: auto;
  }

  .address__subtitle {
    width: auto;
    margin-bottom: 5px;

    font-size: 26px;
    line-height: 30px;
  }

  .address__info {
    display: block;

    margin-bottom: 90px;
  }

  .address__tel {
    display: block;

    width: 100%;
    margin-bottom: 5px;
  }

  .address__subtitile {
    width: auto;
  }

  .address__info:first-of-type {
    margin-right: 20%;
  }
}
