.work {
  margin-bottom: 50px;
}

.work__list {
  margin: 0;
  padding: 0;

  list-style: none;
}

.work__item {
  display: flex;

  margin-bottom: 25px;

  align-items: center;
  justify-content: space-between;

  &:last-of-type {
    margin-bottom: 0;
  }
}

.work__container {
  width: 85%;
}

.work__number {
  display: block;

  font-size: 60px;
  font-weight: 700;

  color: rgb(61,54,54);
}

.work__title {
  position: relative;
}

.work__subtitle {
  margin: 0;
  margin-bottom: 10px;

  font-size: 24px;
  font-weight: 500;
  line-height: 29px;
}

.work__description {
  margin: 0;

  line-height: 22px;
}

@media(min-width: $tablet-width) {
  .work {
    margin-bottom: 150px;
  }

  .work__subtitle {
    font-weight: 600;
  }

  .work-description {
    font-size: 14px;
  }
}

@media(min-width: $desktop-width) {
  .work__list {
    display: flex;

    align-items: flex-start;
  }

  .work__item {
    max-width: 440px;
  }

  .work__subtitle {
    font-size: 40px;
    line-height: 48px;
  }

  .work__number {
    min-width: 70px;

    font-size: 100px;
    line-height: 121px;
  }

  .work__description {
    font-size: 18px;
    line-height: 27px;
  }

  .work__title {
    &::after {
      position: absolute;
      bottom: -30px;
      left: 0;

      width: 500px;
      height: 37px;

      content: '';
      transform: translate(0, 0);

      background: url(../img/line.png) 0 0 no-repeat;
      background-size: cover;
    }
  }
}
