.button {
  position: relative;

  display: block;
  overflow: hidden;

  padding: 2.3rem 4.5rem;
  padding: 10px 20px;

  transition: background-color 0.3s ease-in;
  text-align: center;
  text-decoration: none;

  font-size: 14px;

  color: #ffffff;
  border: 3px solid #ff7e07;
  background-color: transparent;

  svg {
    fill: $orange;
  }

  span {
    position: absolute;
    z-index: 1;
    left: 0;

    width: 100%;

    text-align: center;

    color: #ffffff;
  }

  &:before {
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;

    height: 2px;

    content: '';
    transition: 0.3s ease-in-out;
    transform: translateX(-100%);

    background-color: $white;
  }

  &:hover,
  &:focus {
    font-weight: 600;

    background-color: $orange;

    span {
      font-weight: 600;

      color: $white;
    }

    &::before {
      transform: translateX(0);
    }

    svg {
      fill: $white;
    }
  }
}

.button--active {
  border-color: #fd6c03;
  background-color: #fd6c03;

  &:hover,
  &:focus {
    color: $orange;
    border-color: $white;
    background-color: $white;

    &::before {
      background-color: $orange;
    }
  }
}

@media(min-width: $tablet-width) {
  .button {
    font-size: 20px;
    line-height: 24px;
  }
}
