
.calculator__list {
  margin: 0;
  margin-bottom: 40px;
  padding: 0;

  list-style: none;
}

.calculator__list--colored {
  .calculator__label {
    cursor: pointer;
  }
}

.calculator__item {
  margin-bottom: 20px;
}

.calculator__info {
  display: flex;
}

.calculator__name {
  display: block;

  margin-bottom: 10px;

  font-size: 22px;
  font-weight: 700;
}

.calculator__title--orange {
  color: $orange;
}

.calculator__sum {
  position: relative;

  margin-bottom: 70px;

  text-align: center;

  font-size: 42px;
  font-weight: 800;

  &:before {
    position: absolute;
    bottom: -30px;

    width: 264px;
    height: 17px;

    content: '';
    transform: translate(-50%, 0);

    background: url('../img/line.png') 0 0 no-repeat;
  }

  span {
    display: block;
  }
}

.calculator__value {
  font-size: 32px;
  font-weight: 700;
  line-height: 39px;

  color: $orange;
}

@media(min-width: $tablet-width) {
  .calculator__list {
    display: flex;

    flex-wrap: wrap;
    justify-content: space-between;
  }

  .calculator__item {
    width: 47%;
    margin-bottom: 40px;
  }

  .calculator__title {
    margin-left: 30px;
  }

  .calculator__name {
    font-size: 24px;
    line-height: 29px;
  }

  .calculator__sum {
    margin-left: 30px;

    text-align: left;

    &::before {
      transform: none;
    }

    span {
      display: inline-block;
    }
  }

  .calculator__value {
    font-size: 40px;
  }
}

@media(min-width: $desktop-width) {
  .calculator__services {
    display: flex;

    flex-wrap: wrap;
    align-items: flex-start;
    justify-content: space-between;
  }

  .calculator__list {
    max-width: 1150px;
  }

  .calculator__list--colored {
    max-width: 850px;
    padding-right: 50px;
  }

  .calculator__sum {
    max-width: 330px;
    margin: 0;
    margin-bottom: 60px;

    font-size: 62px;
    line-height: 75px;
  }

  .calculator__value {
    margin: 0;

    font-size: 62px;
    line-height: 75px;
  };

  .calculator__sum:before {
    bottom: -20px;
  }
}
