.title {
  margin: 0;
  margin-bottom: 30px;

  font-size: 28px;
  line-height: 34px;
}

@media(min-width: $tablet-width) {
  .title {
    position: relative;

    margin-bottom: 50px;

    font-size: 36px;
    line-height: 48px;

    &--line::before {
      position: absolute;
      top: 20px;
      left: -30px;

      display: block;

      width: 100vw;
      height: 10px;

      content: '';
      transform: translateX(-100%);

      background-color: #ff7e07;
    }
  }
}

@media(min-width: $desktop-width) {
  .title {
    font-size: 53px;
    line-height: 64px;

    &--big {
      margin-bottom: 75px;

      font-size: 62px;
      line-height: 75px;
    }

    &--line::before {
      top: 30%;
    }
  }
}
