.nav {
  position: absolute;
  top: 0;
  left: 0;

  width: 100%;
  height: 100vh;
  padding-top: 60px;
}

.nav__container {
  padding: 0 30px;
  padding-top: 25px;
}

.nav--show {
  z-index: 1;

  transform: translate(0%);

  opacity: 1;
  background-color: $black;
}

.nav__list {
  margin: 0;
  padding: 0;

  list-style: none;
}

.nav__item {
  margin-bottom: 13px;

  &:nth-last-of-type(2) {
    margin-bottom: 23px;
  }
}

.nav__link {
  position: relative;

  text-decoration: none;

  font-size: 18px;

  color: $white;

  &::before {
    position: absolute;
    right: 0;
    bottom: -4px;
    left: 0;

    width: 0;
    height: 2px;
    margin: auto;

    content: '';
    transition: all 0.5s ease-out;

    color: transparent;
    background: $orange;
  }

  &:hover,
  &:focus {
    color: $orange;

    &:before {
      width: 100%;
    }
  }

  &--tel {
    transition: all 0.5s ease-out;

    font-size: 24px;
    font-weight: 700;
    font-weight: 700;

    &:hover {
      color: $white;
      text-shadow: 3px 2px 1px $orange;
    }
  }
}

@media(max-width: $tablet-width) {
  .nav {
    transition: background-color 0.5s linear,z-index 0.5s linear,transform linear 0.8s,opacity 0.5s linear 0.1s,-webkit-transform linear 0.8s;

    &--hidden {
      z-index: -1;

      -webkit-transform: translate(-100%);
      transform: translate(-100%);

      opacity: 0;
      background-color: transparent;
    }
  }
}

@media(min-width:$tablet-width) {
  .nav {
    display: flex;

    height: auto;
    padding-top: 0;

    flex-grow: 2;
    align-items: center;

    &--hidden {
      position: initial;

      display: flex;

      width: auto;
    }
  }

  .nav__container {
    width: 100%;
    margin: 0;
    padding: 0;
  }

  .nav__list {
    display: flex;

    width: 100%;

    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
  }

  .nav__item {
    margin-bottom: 0;

    &:nth-last-of-type(2) {
      margin-bottom: inherit;
    }
  }

  .nav__link {
    font-size: 12px;

    &--tel {
      font-size: 18px;
    }
  }
}

@media(min-width: $desktop-width) {
  .nav__link {
    font-size: 18px;

    &--tel {
      font-size: 32px;
    }
  }
}
