.label > input {
  position: absolute;
  z-index: -1;

  transition: all 0.5s ease-in;

  opacity: 0;
}

.label > span::before {
  display: inline-block;

  width: 30px;
  height: 30px;
  margin-right: 20px;

  content: '';
  transition: all 0.3s ease-out;

  border: 3px solid #ff7e07;

  flex-shrink: 0;
  flex-grow: 0;
}

.label > input:checked + span::before {
  background-color: #ff7e07;
}

.calculator__list--colored .label > input:checked + span {
  color: #ff7e07;
}
