.about {
  display: none;
}

// @media(min-width: $tablet-width) {
// }
@media(min-width: $desktop-width) {
  .about {
    display: block;

    width: 450px;
  }

  .about__description {
    font-size: 20px;
    line-height: 30px;
  }

  .about__number {
    text-align: center;
  }

  .about__text {
    font-size: 44px;
    line-height: 67px;
  }

  .about__big-text {
    letter-spacing: 0.05em;

    font-size: 62px;
    font-weight: 700;
  }

  .about__big-number {
    font-size: 210px;
    font-weight: 700;
    line-height: 220px;
  }
}
